.product {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    width: 100%;
    background-color: white;
    z-index: 1;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}

.product__price {
    margin-top: 5px;
}

.product__rating {
    display: flex;
}

.product > img {
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;

}

.counter {
    display: flex;
    align-items: center;
    margin-top: 5px;
}

.counter__input {
    width: 25px;
    height: 25px;
    text-align: center;
}

.counter__input::-webkit-outer-spin-button,
.counter__input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.counter__minus,
.counter__plus
{
    margin: 5px;
    border: 1px solid lightgray;
    cursor: pointer;
    padding: 2px;
    border-radius: 4px;
}